@import "../style.scss";

.drop-content-visible {
  display: flex;
  flex-direction: column;

  .page {
    padding: 1rem;
    margin-left: 1rem;
    a,
    a:active {
      font-size: 2rem;
    }

    h2 {
      font-size: 2rem;
      color: $m_grey;
    }
  }
}
.drop-content-hidden {
  display: none;
}
